<template>
  <div class="amount-to-words">
    <h2>金额转大写</h2>
    <input v-model="amount" type="text" placeholder="请输入金额（支持小数）" />
    <div>
      <strong>大写金额：</strong>
      <p>{{ amountToWords }}</p>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      amount: ''
    };
  },
  computed: {
    amountToWords() {
      return this.convertCurrency(this.amount);
    }
  },
  methods: {
    convertCurrency(money) {
      const cnNums = ['零', '壹', '贰', '叁', '肆', '伍', '陆', '柒', '捌', '玖'];
      const cnIntRadice = ['', '拾', '佰', '仟'];
      const cnIntUnits = ['', '万', '亿', '兆'];
      const cnDecUnits = ['角', '分'];
      let integerNum;
      let decimalNum;
      let chineseStr = '';
      const parts = money.split('.');
      if (parts.length > 2) return '';
      integerNum = parts[0];
      decimalNum = parts[1];

      // 整数部分
      if (parseInt(integerNum, 10) > 0) {
        let zeroCount = 0;
        const IntLen = integerNum.length;
        for (let i = 0; i < IntLen; i++) {
          const n = integerNum.substr(i, 1);
          const p = IntLen - i - 1;
          const q = p / 4;
          const m = p % 4;
          if (n === '0') {
            zeroCount++;
          } else {
            if (zeroCount > 0) {
              chineseStr += cnNums[0];
            }
            zeroCount = 0;
            chineseStr += cnNums[parseInt(n)] + cnIntRadice[m];
          }
          if (m === 0 && zeroCount < 4) {
            chineseStr += cnIntUnits[q];
          }
        }
        chineseStr += '元';
      }

      // 小数部分
      if (decimalNum) {
        for (let i = 0; i < decimalNum.length; i++) {
          const n = decimalNum.substr(i, 1);
          if (n !== '0') {
            chineseStr += cnNums[Number(n)] + cnDecUnits[i];
          }
        }
      } else {
        chineseStr += '整';
      }

      return chineseStr || '';
    }
  }
};
</script>

<style scoped>
.amount-to-words {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

input {
  padding: 10px;
  width: 100%;
  box-sizing: border-box;
}
</style>